
























































import { getHistory } from "@/api/apis/inventory.api";
import TableHeader from "@/components/core/TableHeader.vue";
import {
  defaultPage,
  Dialog,
  Pagination,
  TableHeaderModel,
} from "@/components/models";
import { pageListSize } from "@/constants";
import { DIALOG } from "@/store/store_types";
import Vue from "vue";
import { mapMutations } from "vuex";
export default Vue.extend({
  components: { TableHeader },
  data: () => ({
    search: "",
    isLoading: true,
    page: 1,
    pageCount: 1,
    reports: [] as Array<any>,
    headers: [
      {
        text: "تاریخ",
        align: "center",
        sortable: true,
        value: "createDate",
      },
      {
        text: "نوع",
        align: "center",
        sortable: true,
        value: "type",
      },
      {
        text: "توضیحات",
        align: "center",
        sortable: true,
        value: "description",
      },
      {
        text: "تعداد",
        align: "center",
        sortable: true,
        value: "count",
      },
      {
        text: "",
        align: "center",
        sortable: false,
        value: "actions",
      },
    ] as Array<TableHeaderModel>,
  }),
  mounted() {
    this.loadReports(defaultPage);
  },
  methods: {
    ...mapMutations(DIALOG, ["showModal"]),
    pageChange(value: any) {
      this.loadReports({ page: value - 1, count: pageListSize });
    },
    loadReports(pagination: Pagination) {
      this.isLoading = true;
      getHistory(pagination)
        .then((reportRes) => {
          if (reportRes.status) {
            this.page = pagination.page + 1;
            this.pageCount = reportRes.result.pageCount + 1;
            this.reports = reportRes.result.histories;
          }
        })
        .finally(() => (this.isLoading = false));
    },
    showItemList(item: any) {
      const list: Dialog = {
        color: "info",
        title: `موجودی ها`,
        content: {
          component: () => import("@/components/inventory/HistoryList.vue"),
          props: {
            historyList: item.historyList,
          },
        },
      };
      this.showModal(list);
    },
  },
});
